.wrestling-ring {
  width: 80vmin;
  height: 80vmin;
  position: relative;
  margin: 0 auto;
  perspective: 1000px;
}

.ring {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  transform: rotateX(60deg);
}

.mat {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #4a5568;
  border: 5px solid #2d3748;
}

.rope {
  width: 100%;
  height: 5px;
  position: absolute;
  background-color: #fff;
  transform-origin: center;
}

.rope-top { transform: translateY(-20px) rotateX(90deg); }
.rope-middle { transform: translateY(0) rotateX(90deg); }
.rope-bottom { transform: translateY(20px) rotateX(90deg); }

.post {
  width: 10px;
  height: 40px;
  position: absolute;
  background-color: #718096;
}

.post-1 { top: -20px; left: -5px; }
.post-2 { top: -20px; right: -5px; }
.post-3 { bottom: -20px; left: -5px; }
.post-4 { bottom: -20px; right: -5px; }

.wrestler {
  width: 40px;
  height: 60px;
  position: absolute;
  transform: translate(-50%, -50%);
}

.wrestler-body {
  width: 100%;
  height: 40px;
  background-color: #ed8936;
  border-radius: 50% 50% 0 0;
}

.wrestler-head {
  width: 20px;
  height: 20px;
  background-color: #f6ad55;
  border-radius: 50%;
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
}

.wrestler-name {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  font-size: 12px;
  background-color: rgba(0,0,0,0.7);
  padding: 2px 5px;
  border-radius: 3px;
}

.shadow-text {
  text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
}